import React from 'react';
import {
  Box,
  Typography,
  useTheme,
  useMediaQuery,
  Tooltip,
  Card,
  CardContent,
} from '@mui/material';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  ResponsiveContainer,
  TooltipProps,
  Tooltip as RechartsTooltip,
} from 'recharts';

interface ChartData {
  date: string;
  value: number;
  previousValue: number;
  change: number;
  changePercent: number;
}

// API
const generateChartData = (): ChartData[] => {
  const data: ChartData[] = [];
  let currentValue = 10000000;
  let previousValue = currentValue;

  for (let i = 0; i < 12; i++) {
    const date = new Date();
    date.setMonth(date.getMonth() - (11 - i));
    
    // -3%+5%
    const changePercent = (Math.random() * 8 - 3);
    const change = currentValue * (changePercent / 100);
    previousValue = currentValue;
    currentValue += change;

    data.push({
      date: date.toISOString().slice(0, 7), // YYYY-MM
      value: Math.round(currentValue),
      previousValue: Math.round(previousValue),
      change: Math.round(change),
      changePercent: Math.round(changePercent * 100) / 100,
    });
  }
  return data;
};

const chartData = generateChartData();

// Tooltip
type ValueType = number;
type NameType = string;
type CustomTooltipProps = TooltipProps<ValueType, NameType>;

const CustomTooltip: React.FC<CustomTooltipProps> = ({ active, payload, label }) => {
  if (active && payload && payload.length) {
    const data = payload[0].payload as ChartData;
    return (
      <Card sx={{ p: 1.5, boxShadow: 2 }}>
        <Typography variant="subtitle2" gutterBottom>
          {label}
        </Typography>
        <Box sx={{ mt: 1 }}>
          <Typography variant="body2" color="text.secondary">
            
          </Typography>
          <Typography variant="body1" sx={{ fontWeight: 500 }}>
            {data.value.toLocaleString()}
          </Typography>
        </Box>
        <Box sx={{ mt: 1 }}>
          <Typography variant="body2" color="text.secondary">
            
          </Typography>
          <Typography
            variant="body1"
            sx={{
              color: data.change >= 0 ? 'success.main' : 'error.main',
              fontWeight: 500,
            }}
          >
            {data.change >= 0 ? '+' : ''}{data.change.toLocaleString()}
            {' '}
            ({data.change >= 0 ? '+' : ''}{data.changePercent}%)
          </Typography>
        </Box>
      </Card>
    );
  }
  return null;
};

const PortfolioSummaryChart = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));

  // 
  const latestData = chartData[chartData.length - 1];
  const previousData = chartData[chartData.length - 2];
  const yearAgoData = chartData[0];

  // 
  const yearlyChange = latestData.value - yearAgoData.value;
  const yearlyChangePercent = (yearlyChange / yearAgoData.value) * 100;

  return (
    <Box>
      {/*  */}
      <Box sx={{ mb: 2 }}>
        <Typography 
          variant="body2" 
          color="text.secondary"
          sx={{ fontSize: isMobile ? '0.75rem' : '0.875rem' }}
        >
          12
        </Typography>
        <Box sx={{ mt: 0.5 }}>
          <Typography 
            variant="h6"
            sx={{ fontSize: isMobile ? '1.125rem' : '1.25rem' }}
          >
            {latestData.value.toLocaleString()}
          </Typography>
          <Typography
            variant="body2"
            sx={{
              color: yearlyChange >= 0 ? 'success.main' : 'error.main',
              fontWeight: 500,
              fontSize: isMobile ? '0.75rem' : '0.875rem',
            }}
          >
            : {yearlyChange >= 0 ? '+' : ''}{yearlyChange.toLocaleString()}
            {' '}
            ({yearlyChange >= 0 ? '+' : ''}{yearlyChangePercent.toFixed(1)}%)
          </Typography>
        </Box>
      </Box>

      {/*  */}
      <Box sx={{ height: { xs: 200, sm: 250, md: 300 } }}>
        <ResponsiveContainer width="100%" height="100%">
          <AreaChart
            data={chartData}
            margin={{ top: 10, right: 0, left: 0, bottom: 0 }}
          >
            <defs>
              <linearGradient id="colorValue" x1="0" y1="0" x2="0" y2="1">
                <stop
                  offset="5%"
                  stopColor={theme.palette.primary.main}
                  stopOpacity={0.1}
                />
                <stop
                  offset="95%"
                  stopColor={theme.palette.primary.main}
                  stopOpacity={0.01}
                />
              </linearGradient>
            </defs>
            <CartesianGrid
              strokeDasharray="3 3"
              vertical={false}
              stroke={theme.palette.divider}
            />
            <XAxis
              dataKey="date"
              tick={{ fontSize: isMobile ? 10 : 12 }}
              tickFormatter={(value) => value.slice(5)} // MM
            />
            <YAxis
              tick={{ fontSize: isMobile ? 10 : 12 }}
              tickFormatter={(value) => `${(value / 1000000).toFixed(0)}M`}
              width={35}
            />
            <RechartsTooltip<ValueType, NameType>
              content={({ active, payload, label }) => (
                <CustomTooltip active={active} payload={payload} label={label} />
              )}
            />
            <Area
              type="monotone"
              dataKey="value"
              stroke={theme.palette.primary.main}
              fill="url(#colorValue)"
              strokeWidth={2}
            />
          </AreaChart>
        </ResponsiveContainer>
      </Box>

      {/*  */}
      <Box sx={{ mt: 2 }}>
        <Typography
          variant="caption"
          color="text.secondary"
          sx={{ fontSize: isMobile ? '0.625rem' : '0.75rem' }}
        >
           
          
        </Typography>
      </Box>
    </Box>
  );
};

export default PortfolioSummaryChart; 