import React from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  TextField,
  Button,
  Alert,
  Stack,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import { Warning as WarningIcon } from '@mui/icons-material';
import { useParams, useNavigate } from 'react-router-dom';
import { Fund, Position } from '../../types';

// 
const fundData: Fund & Position = {
  id: '1',
  fundId: '1',
  name: '',
  type: '',
  risk: 4,
  currency: '',
  minInvestment: 1000000,
  description: '',
  features: ['', '', ''],
  units: 1000,
  bookValue: 2000000,
  currentValue: 2200000,
  unrealizedGain: 200000,
  unrealizedGainPercent: 10,
};

const SellForm = () => {
  const { fundId } = useParams();
  const navigate = useNavigate();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [units, setUnits] = React.useState<string>('');

  const handleUnitsChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value.replace(/[^\d]/g, '');
    setUnits(value);
  };

  const handleSubmit = () => {
    if (!units) return;
    
    // 
    console.log('Sell:', {
      fundId,
      units: parseInt(units),
    });

    // 
    navigate('/transactions');
  };

  const isValidUnits = parseInt(units) > 0 && parseInt(units) <= fundData.units;
  const estimatedValue = isValidUnits 
    ? Math.round((parseInt(units) / fundData.units) * fundData.currentValue)
    : 0;

  return (
    <Box sx={{ 
      p: { xs: 1.5, sm: 2, md: 3 },
      pt: { xs: '64px', sm: '64px', md: '64px' },
    }}>
      <Typography 
        variant="h4" 
        gutterBottom
        sx={{ fontSize: isMobile ? '1.5rem' : '2.125rem' }}
      >
        
      </Typography>

      <Card sx={{ mb: { xs: 2, sm: 3 } }}>
        <CardContent sx={{ p: { xs: 1.5, sm: 2 } }}>
          <Typography 
            variant="h6"
            sx={{ fontSize: isMobile ? '1rem' : '1.25rem' }}
          >
            {fundData.name}
          </Typography>
          <Box sx={{ mt: 2 }}>
            <Typography 
              variant="body2" 
              color="text.secondary"
              sx={{ fontSize: isMobile ? '0.75rem' : '0.875rem' }}
            >
              
            </Typography>
            <Typography 
              variant="h6"
              sx={{ fontSize: isMobile ? '1rem' : '1.25rem' }}
            >
              {fundData.units.toLocaleString()}
            </Typography>
          </Box>
          <Box sx={{ mt: 1 }}>
            <Typography 
              variant="body2" 
              color="text.secondary"
              sx={{ fontSize: isMobile ? '0.75rem' : '0.875rem' }}
            >
              
            </Typography>
            <Typography 
              variant="h6"
              sx={{ fontSize: isMobile ? '1rem' : '1.25rem' }}
            >
              {fundData.currentValue.toLocaleString()}
            </Typography>
          </Box>
        </CardContent>
      </Card>

      <Card>
        <CardContent sx={{ p: { xs: 1.5, sm: 2 } }}>
          <Stack spacing={2}>
            <Typography 
              variant="h6"
              sx={{ fontSize: isMobile ? '1rem' : '1.25rem' }}
            >
              
            </Typography>
            <TextField
              fullWidth
              label=""
              value={units ? parseInt(units).toLocaleString() : ''}
              onChange={handleUnitsChange}
              error={!!units && !isValidUnits}
              helperText={
                units && !isValidUnits
                  ? ''
                  : ' '
              }
              size={isMobile ? "small" : "medium"}
            />
            {isValidUnits && (
              <Typography 
                variant="body1"
                sx={{ fontSize: isMobile ? '0.875rem' : '1rem' }}
              >
                : {estimatedValue.toLocaleString()}
              </Typography>
            )}
            <Button
              variant="contained"
              color="error"
              fullWidth
              size={isMobile ? "large" : "large"}
              disabled={!isValidUnits}
              onClick={handleSubmit}
            >
              
            </Button>

            <Alert 
              severity="info" 
              icon={<WarningIcon />}
              sx={{ 
                '& .MuiAlert-message': {
                  fontSize: isMobile ? '0.75rem' : '0.875rem',
                },
              }}
            >
              
              
            </Alert>
          </Stack>
        </CardContent>
      </Card>
    </Box>
  );
};

export default SellForm; 