import React from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  List,
  ListItem,
  ListItemText,
  Chip,
  TextField,
  MenuItem,
  Stack,
  useTheme,
  useMediaQuery,
} from '@mui/material';
import {
  Add as AddIcon,
  Remove as RemoveIcon,
  Search as SearchIcon,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import { Transaction } from '../../types';

// 
const transactions: (Transaction & { fundName: string })[] = [
  {
    id: 't1',
    fundId: '1',
    fundName: '',
    type: '',
    status: '',
    amount: 1000000,
    units: 500,
    date: '2024-03-01',
    settlementDate: '2024-03-05',
  },
  // ... 
];

const TransactionHistory = () => {
  const theme = useTheme();
  const navigate = useNavigate();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const [searchTerm, setSearchTerm] = React.useState('');
  const [statusFilter, setStatusFilter] = React.useState<string>('all');
  const [typeFilter, setTypeFilter] = React.useState<string>('all');

  const getStatusColor = (status: Transaction['status']) => {
    switch (status) {
      case '':
        return 'success';
      case '':
        return 'warning';
      case '':
        return 'error';
      default:
        return 'default';
    }
  };

  const getTransactionIcon = (type: Transaction['type']) => {
    return type === '' ? (
      <AddIcon sx={{ color: 'success.main', fontSize: isMobile ? 16 : 20 }} />
    ) : (
      <RemoveIcon sx={{ color: 'error.main', fontSize: isMobile ? 16 : 20 }} />
    );
  };

  const filteredTransactions = transactions.filter(transaction => {
    const matchesSearch = transaction.fundName.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesStatus = statusFilter === 'all' || transaction.status === statusFilter;
    const matchesType = typeFilter === 'all' || transaction.type === typeFilter;
    return matchesSearch && matchesStatus && matchesType;
  });

  return (
    <Box sx={{ 
      p: { xs: 1.5, sm: 2, md: 3 },
      pt: { xs: '64px', sm: '64px', md: '64px' },
    }}>
      <Typography 
        variant="h4" 
        gutterBottom
        sx={{ fontSize: isMobile ? '1.5rem' : '2.125rem' }}
      >
        
      </Typography>

      {/*  */}
      <Card sx={{ mb: { xs: 2, sm: 3 } }}>
        <CardContent>
          <Stack 
            direction={{ xs: 'column', sm: 'row' }} 
            spacing={{ xs: 1.5, sm: 2 }}
          >
            <TextField
              label=""
              variant="outlined"
              size={isMobile ? "small" : "medium"}
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              sx={{ minWidth: { xs: '100%', sm: 200 } }}
              InputProps={{
                startAdornment: (
                  <SearchIcon 
                    sx={{ 
                      color: 'text.secondary', 
                      mr: 1,
                      fontSize: isMobile ? 20 : 24,
                    }} 
                  />
                ),
              }}
            />
            <TextField
              select
              label=""
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
              size={isMobile ? "small" : "medium"}
              sx={{ minWidth: { xs: '100%', sm: 150 } }}
            >
              <MenuItem value="all"></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </TextField>
            <TextField
              select
              label=""
              value={typeFilter}
              onChange={(e) => setTypeFilter(e.target.value)}
              size={isMobile ? "small" : "medium"}
              sx={{ minWidth: { xs: '100%', sm: 150 } }}
            >
              <MenuItem value="all"></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </TextField>
          </Stack>
        </CardContent>
      </Card>

      {/*  */}
      <Card>
        <List sx={{ p: 0 }}>
          {filteredTransactions.map((transaction, index) => (
            <React.Fragment key={transaction.id}>
              {index > 0 && <Box sx={{ my: 1, borderTop: 1, borderColor: 'divider' }} />}
              <ListItem
                sx={{
                  py: { xs: 1.5, sm: 2 },
                  px: { xs: 1.5, sm: 2, md: 3 },
                  '&:hover': { 
                    bgcolor: 'action.hover',
                    cursor: 'pointer',
                  },
                }}
                onClick={() => navigate(`/funds/${transaction.fundId}`)}
              >
                <Box sx={{ mr: 2 }}>
                  {getTransactionIcon(transaction.type)}
                </Box>
                <ListItemText
                  primary={
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 0.5 }}>
                      <Typography 
                        variant="body2" 
                        sx={{ 
                          fontWeight: 500,
                          fontSize: isMobile ? '0.875rem' : '1rem',
                        }}
                      >
                        {transaction.fundName}
                      </Typography>
                      <Chip
                        label={transaction.status}
                        size="small"
                        color={getStatusColor(transaction.status)}
                        variant="outlined"
                        sx={{
                          height: isMobile ? 20 : 24,
                          '& .MuiChip-label': {
                            px: isMobile ? 1 : 2,
                            fontSize: isMobile ? '0.75rem' : '0.875rem',
                          },
                        }}
                      />
                    </Box>
                  }
                  secondary={
                    <Box sx={{ mt: 0.5 }}>
                      <Typography 
                        variant="body2" 
                        color="text.secondary"
                        sx={{ fontSize: isMobile ? '0.75rem' : '0.875rem' }}
                      >
                        {transaction.date}
                        {transaction.settlementDate && 
                          `  : ${transaction.settlementDate}`}
                      </Typography>
                      <Typography 
                        variant="body2" 
                        sx={{ 
                          color: transaction.type === '' ? 'success.main' : 'error.main',
                          fontWeight: 500,
                          mt: 0.5,
                          fontSize: isMobile ? '0.875rem' : '1rem',
                        }}
                      >
                        {transaction.amount.toLocaleString()}
                        {transaction.units && 
                          ` (${transaction.units.toLocaleString()})`}
                      </Typography>
                    </Box>
                  }
                />
              </ListItem>
            </React.Fragment>
          ))}
        </List>
      </Card>
    </Box>
  );
};

export default TransactionHistory; 